'use client'

import Image from 'next/image'
import { Button } from './ui/button'
import { useSession, signIn, signOut } from 'next-auth/react'
import { useState } from 'react'

const Navbar = () => {
  const { data: session } = useSession()

  const [hidden, sethidden] = useState<boolean>(false)

  const forSetHidden = () => {
    sethidden(!hidden)
  }

  return (
    <div className='flex justify-between'>
      <Image
        src='/wongnok-with-name-logo.png'
        width={182}
        height={49}
        alt='wongnok-logo'
      />
      {session ? (
        <div
          className='flex flex-col relative cursor-pointer'
          onClick={forSetHidden}
        >
          <div className='flex h-[40px] items-center justify-center'>
            <div className='text-primary-500'>{session.user?.name}</div>
            <div className='w-[24px] h-[24px] ms-1 flex justify-center items-center'>
              {!hidden && (
                <Image
                  src='/icons/down.svg'
                  width={11}
                  height={7}
                  alt='down menu logout'
                />
              )}
              {hidden && (
                <Image
                  src='/icons/up.svg'
                  width={11}
                  height={7}
                  alt='down menu logout'
                />
              )}
            </div>
          </div>
          {hidden && (
            <div className='w-[195px] h-[96px] border absolute mt-[64px]'>
              <Link href={`${process.env.NEXTAUTH_URL}/api/v1/`}>
                <div className='w-[195px] h-[48px] flex items-center bg-pinklittle '>
                  <span className='text-end w-full px-4'>สูตรอาหารของฉัน</span>
                </div>
              </Link>

              <div
                className='w-[195px] h-[48px] flex items-center '
                onClick={() => signOut()}
              >
                <span className='text-end w-full px-4'>ออกจากระบบ</span>
              </div>
            </div>
          )}
        </div>
      ) : (
        <Button
          className='text-primary-a'
          variant='ghost'
          onClick={() => signIn('keycloak')}
        >
          <Image
            color='red'
            src='/icons/person.svg'
            alt='icon person'
            width={24}
            height={24}
          />
          เข้าระบบ
        </Button>
      )}
    </div>
  )
}

export default Navbar
